<template>
    <yd-layout>
        <div class="schedules resHistory">
            <yd-tab>
                <yd-tab-panel label="参会人" :active="flag=='true'">
                    <div class="mainContentFluid">
                        <div class="fluidWidth personList">
                            <yd-list theme="4" v-if="preGroup == ''">
                                <yd-list-item>
                                    <span slot="title" class="name">暂无数据</span>
                                </yd-list-item>
                            </yd-list>
                            <yd-list theme="4" v-for="(value, index) in preGroup" :key="index" v-else>
                                <yd-list-item>
                                    <img slot="img" :src="conf.signURL+value.IMAGE" class="img">
                                    <span slot="title" class="name" v-text="value.EMPNAME"></span>
                                    <yd-list-other slot="other">
                                        <div>
                                            <span class="department" v-text="value.ORG"></span>
                                        </div>
                                        <div class="confirm" v-text="preStatus[value.STATUS-1]">
                                        </div>
                                    </yd-list-other>
                                </yd-list-item>
                            </yd-list>
                        </div>
                    </div>
                </yd-tab-panel>
                <yd-tab-panel label="签到人" :active="flag=='false'">
                    <div class="mainContentFluid">
                        <div class="fluidWidth personList">
                            <yd-list theme="4" v-if="signGroup == ''">
                                <yd-list-item>
                                    <span slot="title" class="name">暂无数据</span>
                                </yd-list-item>
                            </yd-list>
                            <yd-list theme="4" v-for="(value, index) in signGroup" :key="index" v-else>
                                <yd-list-item>
                                    <img slot="img" :src="conf.signURL+value.IMAGE" class="img">
                                    <span slot="title" class="name" v-text="value.EMPNAME"></span>
                                    <yd-list-other slot="other">
                                        <div>
                                            <span class="department" v-text="value.ORG"></span>
                                        </div>
                                    </yd-list-other>
                                </yd-list-item>
                            </yd-list>
                        </div>
                    </div>
                </yd-tab-panel>
            </yd-tab>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            preGroup: [],
            signGroup: [],
            preStatus: ['已确认参会', '', '已拒绝参会'],
            flag: this.$route.params.flag
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    mounted() {
        let vm = this;
        vm.initPreList();
        vm.initSignList();
        document.setTitle('参会人');
    },
    methods: {
        initPreList() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/predeterminePeopleList', {
                'name': vm.getUserInfo.ssotoken,
                'meetId': vm.$route.params.meetId
            }, function (res) {
                vm.preGroup = res.jsonData;
            })
        },
        initSignList() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/signPeopleList', {
                'name': vm.getUserInfo.ssotoken,
                'meetId': vm.$route.params.meetId
            }, function (res) {
                vm.signGroup = res.jsonData;
            })
        }
    }
}
</script>
<style scoped>
.mainContentFluid {
    margin-top: 0.05rem;
}

.department {
    font-size: 0.26rem;
}

.name {
    font-size: 0.3rem;
    line-height: 0.5rem;
}

.confirm,
.unconfirm {
    position: absolute;
    right: 0;
    top: 0.4rem;
    font-size: 0.26rem;
    color: #369df4;
}

.unconfirm {
    color: #ff2338;
}

.personList .yd-list-theme4 {
    border-bottom: 1px dashed #c7c7c7;
}

.yd-list-theme4 .yd-list-item {
    padding: 7px 0 13px;
}
</style>


